.c_box{

  &_Howto{
    border: 3px solid #32a1da;
    border-radius:6px;
    margin-bottom:45px;
    box-shadow:10px 10px 10px rgba(0,0,0, 0.20);
    &_title{
      position: relative;
      background:#1f7fb0;
      &_txt{
        font-size:20px;
        font-weight:bold;
        color:#fff;
        padding:5px 0 5px 130px;
        @include sp{
          padding: 5px 50px 5px 55px;
        }
      }
      &:before{
        content: '';
        width: 80px;
        height: 80px;
        display: block;
        position:absolute;
        top: -20px;
        left: 26px;
        @include sp{
          width:30px;
          height:30px;
          top:50%;
          right:auto;
          left:10px;
          transform: translateY(-50%);
        }
      }
      &:after{
        content: '';
        width: 80px;
        height: 80px;
        display: block;
        position:absolute;
        top:-40px;
        right:10px;
        @include sp{
          width:30px;
          height:30px;
          top:50%;
          right:10px;
          left:auto;
          transform: translateY(-50%);
        }
      }
      &__setItem01:before{
        background:url(../img/top/icon_setItme01_before.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem01:after{
        background:url(../img/top/icon_setItem01_after.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem02:before{
        background:url(../img/top/icon_setItme02_before.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem02:after{
        background:url(../img/top/icon_setItem02_after.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem03:before{
        background:url(../img/top/icon_setItme03_before.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem03:after{
        background:url(../img/top/icon_setItem03_after.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem04:before{
        background:url(../img/top/icon_setItme04_before.png) no-repeat center center;
        background-size: cover;
      }
      &__setItem04:after{
        background:url(../img/top/icon_setItem04_after.png) no-repeat center center;
        background-size: cover;
      }
    }
    &_detial{
      background: #fff;
      padding:22px 36px 10px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      text-align: center;
      @include sp(){
        padding:22px 7px 10px;
      }
      a{
        text-decoration: none;
      }
      &:before{
        content:"";
        display: block;
        width:274px;
        order:1;
      }
      &:after{
        content:"";
        display: block;
        width:274px;
      }
      &_item{
        width:274px;
        margin-bottom:12px;
        @include tb(){
          width:49%;
        }
        p{
          position: relative;
          border:3px solid #d0d0d0;
          border-radius:5px;
          background:#eaf5fb;
          font-size:16px;
          font-weight:bold;
          color:#128ac7;
          padding:7px;
          box-shadow:2px 2px 2px rgba(0,0,0, 0.1);
          @include tb{
            padding:10px 20px;
          }
          &:after{
            content: "";
            position: absolute;
            right: -7px;
            bottom: -2px;
            width: 0;
            height: 0;
            margin: auto;
            border-style: solid;
            border-color: #28b3e5 transparent transparent;
            border-width: 10px 10px 0;
            transform: rotate(-45deg);
          }
          &:nth-child(3n){
            margin-right:0;
          }
          &__gray:after{
            border-color: #a3a3a3 transparent transparent;
          }
        }
      }
    }
    &__LinkBox{
      border: 3px solid #3bc271;
      .c_box_Howto_title_LinkBox{
        position: relative;
        background:#fff;
        &_txt{
          width:360px;
          height:100px;
          background:url(../img/top/Howto_linkboxtitle.png) no-repeat center center;
          background-size: contain;
          text-align:center;
          font-size:28.5px;
          font-weight:bold;
          color:#fff;
          position: absolute;
          top:-30px;
          left:50%;
          padding-top:10px;
          transform: translateX(-50%);
        }
      }
      .c_box_Howto_detial{
        padding:80px 36px 10px 22px;
        &_item{
          width:215px;
          @include tb{
            width:48%;
          }
          p{
            color:#17a952;
            margin-bottom:20px;
          }
          ul{
            text-align: left;
            padding: 0 0 0 5px;
          }
          li{
            margin-bottom:5px;
          }
          a{
            font-size:15px;
            color:#555555;
            text-decoration: underline;
          }
        }
      }
    }
  }
}