.c_button{
  &_anchor{
    display: inline-block;
    width:80px;
    height:90px;
    background:url(../img/common/button_top.png) no-repeat center center;
    position:fixed;
    bottom:30%;
    right:0;
  }
  &_wrapper{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-bottom:30px;
  }
  &_item{
    width: 280px;
    padding: 5px;
    background: linear-gradient(to bottom,#61d1fe 0,#158bb9 100%);
    border-radius: 25px;
    a{
      text-decoration: none;
    }
    &_txt{
      background: #fff;
      border-radius: 22px;
      padding: 11px;
      text-align: center;
      span{
        font-size: 24px;
        color: #2db1e5;
      }
    }
  }
}