/* SP専用クラアス */
@include sp(){
  @for $i from 0 through 20{
    .u_mt#{($i * 5)}sp{ margin-top: #{($i * 5)}px !important; }
    .u_mr#{($i * 5)}sp{ margin-right: #{($i * 5)}px !important; }
    .u_mb#{($i * 5)}sp{ margin-bottom: #{($i * 5)}px !important; }
    .u_ml#{($i * 5)}sp{ margin-left: #{($i * 5)}px !important; }
    .u_pt#{($i * 5)}sp{ padding-top: #{($i * 5)}px !important; }
    .u_pr#{($i * 5)}sp{ padding-right: #{($i * 5)}px !important; }
    .u_pb#{($i * 5)}sp{ padding-bottom: #{($i * 5)}px !important; }
    .u_pl#{($i * 5)}sp{ padding-left: #{($i * 5)}px !important; }
  }
  .u_spHide{ display: none !important; }
};

