/* 全デバイス共通 */
@for $i from 0 through 20{
  .u_mt#{($i * 5)}{ margin-top: #{($i * 5)}px !important; }
  .u_mr#{($i * 5)}{ margin-right: #{($i * 5)}px !important; }
  .u_mb#{($i * 5)}{ margin-bottom: #{($i * 5)}px !important; }
  .u_ml#{($i * 5)}{ margin-left: #{($i * 5)}px !important; }
  .u_pt#{($i * 5)}{ padding-top: #{($i * 5)}px !important; }
  .u_pr#{($i * 5)}{ padding-right: #{($i * 5)}px !important; }
  .u_pb#{($i * 5)}{ padding-bottom: #{($i * 5)}px !important; }
  .u_pl#{($i * 5)}{ padding-left: #{($i * 5)}px !important; }
