.c_detail{
  &_wrapper{
    padding:40px 80px;
    border: 3px solid #32a1da;
    border-radius:6px;
    background:#fff;  
    margin-bottom:45px;
    box-shadow:5px 20px 20px rgba(0,0,0, 0.30);
    @include sp(){
      padding:20px;
    }
  }
  &_title{
    margin-bottom:45px;
    @include sp(){
      margin-bottom:20px;
    }
    &_txt{
      position: relative;
      span{
        color: #32a1da;
        font-size: 40px;
        padding-left: 30px;
        @include sp(){
          font-size:20px;
        }
        &:before{
          content:"";
          display: block;
          width:8px;
          height:47px;
          background:#32a1da;
          position: absolute;
          top:50%;
          left:0;
          transform: translateY(-50%);
        }
      }
    }
  }
  &_contens{
    &_txt{
      margin-bottom:35px;
      line-height: 27px;
    }
    &_ImgBox{
      background:url(../img/detail/detail_net_playable.png) no-repeat center center;
      background-size: contain;
      height:535px;
      border: 2px solid #dcdcdc;
    }
  }
}