/******** top heding **********/
.top{
  &_heading{
    padding-left:20px;
    position: relative;
    @include tb(){
      padding-left:0;
      @include sp{
        padding:50px 0 0 0;
      }
    }
  }
  &_title{
    span{
      padding: 0 0 0 5px;
      font-size:18.5px;
      color:#193491;
      text-shadow:    
      #fff 2px 0px 2px, #fff -2px 0px 2px,
      #fff 0px -2px 2px,#fff -2px 0px 2px,
      #fff 2px 2px 2px, #fff -2px 2px 2px,
      #fff 2px -2px 2px,#fff -2px -2px 2px,
      #fff 1px 2px 2px, #fff -1px 2px 2px,
      #fff 1px -2px 2px,#fff -1px -2px 2px,
      #fff 2px 1px 2px, #fff -2px 1px 2px,
      #fff 2px -1px 2px,#fff -2px -1px 2px,
      #fff 1px 1px 2px, #fff -1px 1px 2px,
      #fff 1px -1px 2px,#fff -1px -1px 2px;
      @include sp{
        font-size:14px;
      }
    }
    &_Img{
      @include tb(){
        width:79%;
        @include sp{
          width:100%;
        }
      }
    }
  }
  &_circleBox{
    width:135px;
    height:135px;
    font-family: '小塚ゴシック Pro L','Kozuka Gothic Pro Light',sans-serif;
    background: linear-gradient(to bottom, #fffd3f 1%,#e3c101 100%); 
    border-radius: 50%;
    position: absolute;
    top: 0px;
    right: 10px;
    padding: 30px 10px;
    text-align: center;
    line-height: 25px;
    @include tb(){
      width:130px;
      height:130px;
      top: 0px;
      right: 0px;
      @include sp{
        width:100px;
        height:100px;
        padding:10px 0;
      }
    }
    p{
      text-shadow:    
      #fff 2px 0px 2px, #fff -2px 0px 2px,
      #fff 0px -2px 2px,#fff -2px 0px 2px,
      #fff 2px 2px 2px, #fff -2px 2px 2px,
      #fff 2px -2px 2px,#fff -2px -2px 2px,
      #fff 1px 2px 2px, #fff -1px 2px 2px,
      #fff 1px -2px 2px,#fff -1px -2px 2px,
      #fff 2px 1px 2px, #fff -2px 1px 2px,
      #fff 2px -1px 2px,#fff -2px -1px 2px,
      #fff 1px 1px 2px, #fff -1px 1px 2px,
      #fff 1px -1px 2px,#fff -1px -1px 2px;
      color:#139bed;
    }
    &_ruby{
      font-size:11px;
      @include tb(){
        font-size:10px;
      }
    }
    &_title{
      font-size:34px;
      @include tb(){
        font-size:31px;
        @include sp{
          font-size:24px;
        }
      }
    }
    &_titleSub{
      font-size:12px;
      @include tb(){
        font-size:11px;
      }
    }
  }
  &_description{
    position: relative;
    margin-bottom: 20px;
    &_inner{
      padding:10px;
      div{
        width: 140px;
        height: 130px;
        position: absolute;
        @include tb(){
          width: 125px;
          height:100px;
        }
      }

    }
    &_text{
      font-size:17px;
      text-align: center;
      @include tb(){
        text-align:left;
        font-size:22px;
        @include sp{
          font-size:14px;
        }
        span{
          font-size:22px;
          @include sp{
            font-size:14px;
          }
        }
        span:before {
          content: "\A" ;
          white-space: pre ;
        }
      }
      @include sp(){
        font-size:18px;
        padding-bottom:80px;
        span{
          font-size:18px;
        }
        @include sp{
          padding-bottom:95px;
        }
      }
      span{
        font-size: 17px;
      }    
    }
    &_Imgpacket{
      background:url(../img/top/top_paket.png) no-repeat center center;
      background-size: contain;
      top:-15px;
      left:0;
      @include tb(){
        top: 20px;
        right:13%;
        left:auto;
        @include sp{
          top:auto;
          bottom:0;
          right:auto;
          left:0;
        }
      }
    }
    &_Imgpackeko{
      background:url(../img/top/top_pakeko.png) no-repeat center center;
      background-size: contain;
      top:-15px;
      right:0;
      @include tb(){
        top:43%;
        @include sp{
          top:auto;
          bottom:0;
        }
      }
    }
  }
}