
body{
  background-repeat: no-repeat;
  background:url(../img/common/pattern_bk.png);
  position: relative;
}


/**** l_inner ****/
.l_inner {
  width:100%;
  @include tb() {
    width: 100%;
  }
}

/***** header ******/
.l_header{
  width:100%;
  min-width: 980px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0,0,0,0.2);
  @include tb(){
    min-width:auto;
  }
  &_inner{
    padding: 6px 6px 6px 60px;
    position: relative;
  }
  &_logo{
    width:136px;
    height:48px;
  }
}
/***** heading *******/
.l_heading{
  background:url(../img/common/heading_sky_bk.png) no-repeat top center;
  background-size:100% 178px;
  min-width: 980px;
  @include tb(){
    min-width:auto;
    width:100%;
  }
  &_inner{
      background: url(../img/common/heading_bk.png) no-repeat center 30px;
      background-size:100% 286px;
      @include tb(){
        background-size: 100% 160px;
        background-position: center 140px;
      }
  }  
  &_content{
    width:980px;
    min-height: 320px;
    margin:0 auto;
    @include tb{
      width:90%;
    }
  }
}
.l_gnav{
    margin-bottom:20px;
    padding:20px;
  ul{
    li{
      position: relative;
      display:inline-block;
      padding:5px 15px;
      margin-right:20px;
      font-size:20px;
      color:#1c8fc9;
      @include tb{
        font-size:18px;
        margin:0;
        @include sp{
          font-size:14px;
          padding:0 15px;
        }
      }
      &:after{
        content:"";
        display: block;
        width:2px;
        height:25px;
        background:#1c8fc9;
        position: absolute;
        top:50%;
        right:-10px;
        transform: translateY(-50%);
        @include tb(){
          right: -2px;
          @include sp{
            display:none;
          }
        }
      }
      &:nth-child(3):after
      ,&:nth-child(6):after
      ,&:nth-child(9):after{
        display:none;
      }
      @include tb(){
        &:nth-child(2):after
        ,&:nth-child(4):after
        ,&:nth-child(7):after{
          display:none;
        }
        &:nth-child(3):after
        ,&:nth-child(6):after{
          display:block;
          @include sp{
            display:none;
          }
        }
      }
    }
  }
  &_inner{
    background:#fff;
    border-radius: 5px;
    padding:20px;
    @include sp{
      padding:10px;
    }
  }
}


/***** main ******/
