@mixin tb {
  @media (max-width: ($breakpoint_tb)) {
    @content;
  }
}
@mixin sp {
  @media (max-width: ($breakpoint_sp)) {
    @content;
  }
}
@mixin pc {
  @media (min-width: ($breakpoint_tb)) {
    @content;
  }
}

@mixin absolute-centering($horizontal: true, $vertical: true) {
	position: absolute;
	@if $horizontal and $vertical {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	} @else if $horizontal {
		left: 50%;
		transform: translate(-50%, 0);
	} @else if $vertical {
		top: 50%;
		transform: translate(0, -50%);
	}
}

@mixin text-truncate {
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

@mixin responsive-iframe($ratio:56.25%){
  position: relative;
  width: 100%;
  padding-top: $ratio;
  & iframe{
    position: absolute;
    top: 0;
    right: 0;
    width: 100% !important;
    height: 100% !important;
  }
}
