.c_breadcrumb{
  @include sp() {
    display:none;
  }
  &_inner{
    display:flex;
    justify-content:margin-right auto;
    margin-bottom: 18px;
  }
  &_list{
    padding:20px 20px 20px 0;
    position:relative;
    a{
      text-decoration: none;
      color:#000;
     }
     &:not(:last-child):after{
      content: "\f105";
      font-family: FontAwesome;
      position: absolute;
      top: 19px;
      right: 7px;
    }   
  }
}