/** menu button **/
.c_menuButton{
  border: 6px solid #fff;
  border-radius:20px;
  background: linear-gradient(to bottom, #17a3eb 0%,#1f7fb0 100%);
  box-shadow:10px 10px 10px rgba(0,0,0, 0.16);
  @include tb(){
    margin-bottom:20px;
  }
  @include sp(){
    width:100%;
  }
a{
  text-decoration: none;
}
&_wrapper{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
&_detail{
  padding:17px 30px;
  position: relative;
  @include tb(){
    padding: 17px 58px;
  }
  &:after{
    content: "";
    position: absolute;
    right: -5px;
    bottom: 5px;
    width: 0px;
    height: 0px;
    margin: auto;
    border-style: solid;
    border-color: #fff transparent transparent transparent;
    border-width: 20px 20px 0 20px;
    transform: rotate(-45deg);
    opacity: 0.7;
  }
  &_txt{
    width: 160px;
    @include tb(){
      width: 208px;
    }
    span{
      font-size:20px;
      color:#fff;
      font-weight: bold;
      text-shadow:2px 2px 2px rgba(0,0,0, 0.1);
      @include tb(){
        font-size:26px;
      }
    }
  }
}
}